.services__container{
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   gap: 3rem;
}
.services{
   background: var(--color-bg-variant);
   border-radius: 0 0 2rem 2rem;
   border: 1px solid var(--color-primary);
   height: fit-content;
   transition: var(--transition);
}
.services:hover {
   background: transparent;
   border-color: var(--color-primary-variant);
   cursor: default;
}
.service__head{
   background: var(--color-primary);
   padding: 2rem;
   border-radius: 0 0 2rem 2rem;
   box-shadow: 0 2rem 1rem rgba(0, 0, 0, 0.1);
}
.service__head h3 {
   color: var(--color-bg);
   font-size: 1rem;
   text-align: center;
}
.service__list{
   padding: 2rem;
}
.service__list li {
   display: flex;
   gap: 1rem;
   margin-bottom: 0.8rem;
}
.service__list-icon {
   color: var(--color-primary);
   margin-top: 2px;
}
.service__list p {
   font-size: 0.9rem;
}



/* uiux logo */
.uiux__logo {
   height: 50px;
   width: 50px;
   border-radius: 20%;
   margin-left: 7.5rem;
   position: relative;
   bottom: 10px;
   /* left: 2px; */
   
}
/* =======MEDIUM DEVICES========== */
@media screen and (max-width: 1024px){
   .services__container {
      grid-template-columns: 1fr 1fr;
      gap: 2rem;
   }
   .services {
      height: auto;
   }
}

/* SMALL DEVICES */

@media screen and (max-width: 600px){
   .services__container {
      grid-template-columns: 1fr;
      gap: 1.5rem;
   }
}